<script>
import Sortable from "sortablejs";
import {
  APPKEY,
  APPSECRET,
  shopBaseUrl,
  attributesListUrl,
  attributesIMforUrl
} from "../../../api/api";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      formInline: {
        name: "",
        productCategory: "",
        state: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      dialogTableVisible: false,
      dialogFormVisible: false,
      addTime: "",
      tableData: [],
      breadcrumbTitle: "",
      pickerOptions2: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      active: 0,
      current: 1,
      pageSize: 15,
      multipleSelection: "",
      total: 0,
      loading: true
    };
  },
  //
  computed: {},
  watch: {},
  created() {},
  mounted() {
    this.getAttributesList();
  },
  //keep-alive 
  activated() {},
  //keep-alive 
  deactivated() {},
  //
  methods: {
    
    handleBtn(type) {
      this.dialogFormVisible = true;
      
    },

    // 
    getAttributesList() {
      this.loading = true;

      this.$ajax({
        method: "get",
        url: attributesListUrl,
        params: {
          page: this.current - 1,
          size: this.pageSize
        }
      })
        .then(res => {
          if (res.data.code === 0) {
            this.tableData = [];
          } else {
            this.tableData = res.data.content;
            this.total = res.data.totalElements;
          }
          this.loading = false;
        })
        .catch(error => {
          console.log(error);
        });
    },
    selectTime() {
      console.log(this.formInline.addTime.join(","));
    },

    handleSizeChange(val) {
      this.loading = true;
      this.pageSize = val;
      this.getAttributesList();
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      this.current = val;

      this.getAttributesList();
      console.log(`: ${val}`);
    },
    handleEdit(index, row) {
      console.log(index, row.attributeId);
      this.$router.push({
        path: "/store/attribute-add",
        query: {
          id: row.attributeId
        }
      });
    },
    handleDel(index, row) {
      this.$ajax({
        method: "delete",
        url: attributesIMforUrl + row.attributeId,
        params: {
          appKey: APPKEY,
          appSecret: APPSECRET
        }
      })
        .then(res => {
          if (res.status === 200) {
            this.$message.success("");
            this.getAttributesList();
          }
        })
        .catch(error => {
          console.log(error.response);
          this.$message.error("");
        });
    },
    handleSelectionChange(val) {
      console.log(val);
      let productId = [];
      if (val.length != 0) {
        for (let key in val) {
          productId.push(val[key].productId);
          this.multipleSelection = productId.join(",");
        }
      } else {
        this.multipleSelection = "";
      }

      console.log(this.multipleSelection);
    }
  }
};
</script>
